.menu {
  width: 100vw;
  max-width: 100vw;
  height: 100vh;
  height: calc(var(--vh, 1vh) * 100);
  position: absolute;
  top: 0px;
  left: 0px;
  transform: translate(300%, -30%);
  background: rgba(5, 5, 5, .9);
  color: white;
  z-index: 11;
  transition: all .2s;
  overflow: auto;
  display: none;
}
  .menu__list {
    display: block;
    list-style-type: none;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 60px 0 0 0;
    text-align: left;
    margin: 0;
  }
    .menu__item {
      
    }
      .menu__link {
        text-decoration: none;
        color: inherit;
        text-transform: uppercase;
        display: block;
        padding: 30px 30px;
      }

.menu_visible {
  transform: translate(0%);
  display: block;
}



    /* menu MEDIA */
    @media screen and (min-width: 576px){

    }
    @media screen and (min-width: 992px){
      .menu {
        position: relative;
        height: auto;
        transform: none;
        background: transparent;
        width: auto;
        margin-left: auto;
        display: block;
      }
      .menu__list {
        flex-direction: row;
        padding: 0;
      }
    }
